.Home{
  max-width: 1300px;
  margin: 0 auto;
  margin-bottom: 4rem;
}
.my_box{
  position: relative;
  margin-top: 2rem;
}
.video{
  width: 100%;
}
.btn_my{
  display: flex;
  align-items: center;
  justify-content: center;
  display: inline-block;
  padding: .5rem 2rem;
  border: 1px solid #fff;
  font-family: 'Atmospheric';
  font-weight: normal;
  font-size: .9rem;
  border-radius: 2rem !important;
  cursor: pointer;
  background: transparent;
}
.btn1{
  position: absolute;
  bottom: 1.5rem;
  left: 50%;
  transform: translateX(-50%);
}
.btn1 img{
  width: 2.5rem;
  min-width: 24px;
  margin-right: 1rem;
  display: inline-block;
}


.btn_leaen{
  position: absolute;
  bottom: 2rem;
  right: 4rem;
  text-align: center;
}

.btn_leaen:hover{
  background: #fff;
  color: #333;
}



@media screen and (max-width: 850px) and (orientation: portrait) {
  .btn_my{
    padding: .25rem 1rem;
    font-size: .45rem;
    border-radius: 1rem !important;
  }
  .btn1{
    bottom: .75rem;
  }
  .btn1 img{
    width: 1.7rem;
    margin-right: .5rem;
  }
  .btn_leaen{
    bottom: 1rem;
    right: 2rem;
  }
}
@media screen and (max-width: 450px) and (orientation: portrait) {
  .btn_my{
    padding: .15rem .4rem;
    font-size: .2rem;
  }
  .btn1{
    bottom: .4rem;
  }
  .btn1 img{
    width: .7rem;
    margin-right: .25rem;
    
  }
  .btn_leaen{
    bottom: .5rem;
    right: 1rem;
  }
}
