.header{

}
.site-layout-content {
  min-height: 280px;
  padding: 24px;
}
#components-layout-demo-top .logo {
  float: left;
  width: 120px;
  height: 31px;
  margin: 16px 24px 16px 0;
  background: rgba(255, 255, 255, 0.3);
}
.ant-row-rtl #components-layout-demo-top .logo {
  float: right;
  margin: 16px 0 16px 24px;
}




/*  */
.navbar{
  background-color: var(--mainColor);
  border-bottom: 1px solid #fff;
  color: #fff;
}
.header_icon{
  display: flex;
  align-items: center;
  font-style: italic;
  font-weight: normal;
}
.header_icon img{
  width: 1.4rem;
  margin-right: 0.3rem;

}

.bg_main{
  color: #000;
  /* background-color:  var(--mainColor); */
}
.menu_font{
  font-family: 'Atmospheric';
  font-weight: normal;
}
.header_icon_box ul{
  display:flex;
  align-items: center;
}
.header_icon_box img{
  width: 38px;
  margin-right: 10px;
  cursor: pointer;
}

.navbar-end button,.navbar-end button:hover,.navbar-end button:focus{
  border: 1px solid #fff;
  font-family: 'Atmospheric';
  font-weight: normal;
  font-size: 14px;
  border-radius: 20px !important;
  height: 40px !important;
}
