.my_footer {
  width: 100%;
  /* margin-top: 4rem; */
  padding: 3rem 0;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #5024ab;
}

.footer_main {
  width: 90%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

h1 {
  font-size: 2rem;
  color: #fff;
  margin-bottom: 1rem;
  margin-top: 2rem;
}

p {
  font-size: 1rem;
  color: #fff;
  width: 70%;
  text-align: center;
  margin-bottom: 1rem;
}

.myForm {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}

.myForm input {
  padding: 0.8rem;
  height: 3rem;
  width: 18rem;
  border: none;
  background-color: hsla(0, 0%, 100%, .253);
  border-radius: 0.5rem;
  outline: none;
}

.myForm button {
  height: 3rem;
  background-color: #ff4800;
  border: none;
  width: 7rem;
  color: #fff;
  margin-left: 0.5rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

@media screen and (max-width: 450px) and (orientation: portrait) {
  h1 {
    font-size: 1rem;
    margin-bottom: 0.5rem;
  }

  p {
    font-size: .8rem;
    margin-bottom: 0.5rem;
    width: 90%;
  }

  .myForm input {
    height: 2.2rem;
    width: 12rem;
    font-size: .8rem;
  }

  .myForm button {
    height: 2.2rem;
    width: 7em;
    font-size: .8rem;
  }
}